/*
* 营业收入表
* dangwbocspace.cn
* */
import React from 'react';
import {Breadcrumb, Col, Row, Table, Card, Affix, Menu, Dropdown, Button, Icon, ConfigProvider} from 'antd';
import ReactEcharts from 'echarts-for-react';
import {Scrollbars} from 'react-custom-scrollbars';
import url from 'url';
import moment from 'moment';
import {inject} from "mobx-react";

const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@inject('kanbanStore')
export default class Yysr extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			yearData: [{
				id: '',
				name: '2019'
			}],  // 年份
			selData: [
				{
					id: '01',
					name: '1月'
				}, {
					id: '02',
					name: '2月'
				}, {
					id: '03',
					name: '3月'
				}, {
					id: '04',
					name: '4月'
				}, {
					id: '05',
					name: '5月'
				}, {
					id: '06',
					name: '6月'
				}, {
					id: '07',
					name: '7月'
				}, {
					id: '08',
					name: '8月'
				}, {
					id: '09',
					name: '9月'
				}, {
					id: '10',
					name: '10月'
				}, {
					id: '11',
					name: '11月'
				}, {
					id: '12',
					name: '12月'
				}, {
					id: 'season1',
					name: '1季度'
				}, {
					id: 'season2',
					name: '2季度'
				}, {
					id: 'season3',
					name: '3季度'
				}, {
					id: 'season4',
					name: '4季度'
				}, {
					id: 'fullyear',
					name: '年度'
				}
			],
			value: '年度',
			value1: '2019',
			id: '', // 合同表id
			time: null, // 最后更新时间
			capitalData: [], // 数据
			xAxis: [], // x轴
			series: [], // 图表数据
			legend:[],
			columns: [],
			key:"1",
			month:''
		};
	}

	componentDidMount() {
        // 获取当前年份数据
		const currentYear = moment().year();
		/*
		* 年份数据来源：从2019年开始， 截至到当前的年份
		* */
		if (currentYear<=2019) {
			this.setState({
				yearData: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i=2019;i<=num;i++) {
				data.push({name:String(i)})
			}
			this.setState({
				yearData: Object.assign([], data.reverse())
			});
		}
		const data = {
			'01': '1月',
			'02': '2月',
			'03': '3月',
			'04': '4月',
			'05': '5月',
			'06': '6月',
			'07': '7月',
			'08': '8月',
			'09': '9月',
			'10': '10月',
			'11': '11月',
			'12': '12月'
		}
		const mont = moment().month() + 1;
		let search = this.props.location.search;
		const id = url.parse(search, true).query.id;
		const time = url.parse(search, true).query.time;
		const key = url.parse(search, true).query.key;
		const mon = url.parse(search, true).query.month;
		let month = mon == undefined ? mont : mon;
		let lastTime = null; // 最后更新时间

		// 没有时间
		if(time==undefined) {
			// 根据id查询时间
			this.getLastUpdatedTime().then((res) => {
				if (res) {
					let capitalInfo = res.filter(item => {
						return item.code === "yysr"
					});
					if (capitalInfo.length > 0) {
						lastTime = capitalInfo[0]['lastUpdateTime'];
						this.setState({
							time: Number(lastTime)
						})
					}
				}
			});
		} else {
			this.setState({
				time: Number(time)
			})
		}

		this.setState({
			id,
			key,
			value: data[month],
			month: month.toString(),
			value1: currentYear.toString()
		}, () => {
			// 获取初始数据
			this.getReportByTime();
		});
	}

	componentWillUnmount() {

	}

	// 获取最后时间
	getLastUpdatedTime = () => {
		const params = {
			"level": 2,
			"parentId": ""
		}
		return this.props.kanbanStore.getLastTime(params);
	}

	getReportByTime = () => {
		const {id, month, value1} = this.state;
		let params = {"classifyId": id, "year": value1, "beginMonth": month, "endMonth": month};
		if (month === "season1") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "03"};
		} else if (month === "season2") {
			params = {"classifyId": id, "year": value1, "beginMonth": "04", "endMonth": "06"};
		} else if (month === "season3") {
			params = {"classifyId": id, "year": value1, "beginMonth": "07", "endMonth": "09"};
		} else if (month === "season4") {
			params = {"classifyId": id, "year": value1, "beginMonth": "10", "endMonth": "12"};
		} else if (month === "fullyear") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "12"};
		}

		this.props.kanbanStore.getReportByClassifyId(params).then((res) => {
			if (res) {
				this.setState({
					capitalData: Object.assign([], res[0]['datas']),
					xAxis: Object.assign([], res[0]['charts'].category), // x轴
					series: Object.assign([], res[0]['charts'].series.map((item) => {
						return {
							name: item.name,
							type: 'bar',
							data: item.value,
							barWidth: 10,
							itemStyle: {
								emphasis: {
									barBorderRadius: 3
								},
								normal: {
									barBorderRadius: 3
								}
							}
						}
					})), // 图表数据
					legend: res[0]['charts'].series.map((item) => {return item.name}),
					columns: res[0].schemas.map((item, vindex)=> {
						return{
							title: item.title,
							dataIndex: item.name,
							align: 'center',
							className: (vindex==0||vindex==1)?'vt w15':'vt w14',
							render: (text, row, index) => {
								if (vindex == 0) {
									if (index == 0) {
										return {
											children: <span>合计</span>,
											props: {
												colSpan: 2,
												className: 'th_bg vt w30'
											}
										};
									} else if (index == 1) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 6,
												className: 'th_bg vt w15'
											}
										};
									} else if (index == 7) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 5,
												className: 'th_bg vt w15'
											}
										};
									} else if (index == 12) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 11,
												className: 'th_bg vt w15'
											}
										};
									} else if (index == 23) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 8,
												className: 'th_bg vt w15'
											}
										};
									} else if (index == 31) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 6,
												className: 'th_bg vt w15'
											}
										};
									} else if (index == 37) {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 6,
												className: 'th_bg vt w15'
											}
										};
									} else {
										return {
											children: <span>{text}</span>,
											props: {
												rowSpan: 0,
												className: 'th_bg vt w15'
											}
										}
									}
								} else if (vindex == 1){
									const obj = {
										children: text,
										props: {
											className: 'th_bg w15'
										},
									};
									if (index == 0) {
										obj.props.colSpan = 0;
									}
									return obj;
								} else {
									return text;
								}
							},
							children: item.children!==null?item.children.map((v)=>{
								return {
									title: v.title,
									dataIndex: v.name,
									align: 'center',
									className: 'vt th_bg1 w14',
								}
							}): null
						}
					}),
				})
			}
		});
	}

	handleMenuClick = (e) => {
		let value = e.key;
		this.setState({
			value: e.item.props.children,
			month: value
		}, ()=>{
			this.getReportByTime();
		});
	}

	// 切换年份
	handleYearClick = (e) => {
		this.setState({
			value1: e.item.props.children
		},()=>{
			this.getReportByTime();
		})
	}

	render() {
		const {yearData, selData, value, value1, time, capitalData, xAxis, series, legend,key} = this.state;
		let option = {
			color: ['#47BAC1', '#7FC8EA', '#B5E3E5'],
			title: {
				text: '营业收入显示板',
				left:'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'axis',
				axisPointer: {
					type: 'shadow'
				}
			},
			legend: {
				data: legend,
				y: 'bottom',
			},
			grid: {
				left: '2%',
				right: '4%',
				containLabel: true
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {show: false, type: ['line', 'bar']},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			xAxis: [
				{
					type: 'category',
					data: xAxis,
				}
			],
			yAxis: [
				{
					type: 'value',
					scale : true,
					axisLine:{
						show:false
					},
					axisTick:{
						show:false
					},
				}
			],
			series: series
		};
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleMenuClick}>
					{selData.map((item, index) => {
						return (
							<Menu.Item key={item.id}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const menu1 = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{yearData.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const {defaultKey} = this.props.kanbanStore;


		return (
			<div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/home?key=" + defaultKey}>经营看板</Breadcrumb.Item>
						<Breadcrumb.Item href={"#/home?key=" + key}>财务</Breadcrumb.Item>
						<Breadcrumb.Item>营业收入表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Row>
					<Col span={24}>
						<div className="mt20 mb20">
							<span className="con_title">营业收入表 </span>
							<span className="con_time ml35">最近更新时间:</span><span
							className="con_time_content ml15">{moment(time).format("YYYY-MM-DD HH:mm:ss")}</span>
						</div>
					</Col>
					<Col span={24}>
						<Dropdown overlay={menu1}>
							<Button style={{width: 82}}>
								{value1} <Icon type="down"/>
							</Button>
						</Dropdown>
						<Dropdown overlay={menu}>
							<Button className="ml15" style={{width: 82}}>
								{value} <Icon type="down"/>
							</Button>
						</Dropdown>
					</Col>
				</Row>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">营业收入情况（单位：万元）</h3>
					<Row>
						<Col span={10}>
							<Affix offsetTop={120}>
								<ReactEcharts style={{height:'400px'}}
								              ref={(node) => { this.charts = node }}
								              notMerge={true}
								              lazyUpdate={true}
								              option={option}/>
							</Affix>
						</Col>
						<Col span={14}>
							<Affix offsetTop={60}>
							<Table columns={this.state.columns}
							       dataSource={null}
							       size={"small"}
							       pagination={false}
							       title={() => '营业收入明细表'}
							       className="ci_table2 ci_table4 ci_table6"
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
								       return className
							       }}
							       bordered/>
							</Affix>
							<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table columns={this.state.columns}
							       dataSource={capitalData}
							       size={"small"}
							       showHeader={false}
							       pagination={false}
							       className="ci_table2 ci_table4 ci_table7"
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
								       return className
							       }}
							       bordered/>
							</ConfigProvider>
							{/*<ConfigProvider renderEmpty={customizeRenderEmpty}>
							<Table columns={this.state.columns}
							       dataSource={capitalData}
							       size={"small"}
							       pagination={false}
							       title={() => '营业收入明细表'}
							       className="ci_table2 ci_table4"
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'shallow_blue';
								       return className
							       }}
							       bordered/>
							</ConfigProvider>*/}
						</Col>
					</Row>
				</Card>
			</div>
		);
	}
}





